@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {

  .bg-bright-purp-600 {
    --tw-bg-opacity: 1;
    background-color: rgba(148, 51, 145, var(--tw-bg-opacity));
  }

  .bg-bright-purp-600 {
    --tw-bg-opacity: 1;
    background-color:  rgba(148, 51, 145, var(--tw-bg-opacity));
  }

  .bg-bright-blue-600 {
    --tw-bg-opacity: 1;
    background-color: rgba(85,146,217, var(--tw-bg-opacity));
  }
  .bg-blue-footer {
    --tw-bg-opacity: 1;
    background-color: rgba(23,49,82, var(--tw-bg-opacity));
  }

  .lavand-color {
    background-color: rgba(212, 238, 251, 1);
  }

  .container-fluid {
    max-width: 100%;
  }

  .border-purple-cstm {
    --tw-border-opacity: 1;
    border-color: rgb(148, 51, 145);
  }

  .bgc-purple {
    background-color: rgb(148, 51, 145);
  }

  .border-blue-cstm {
    --tw-border-opacity: 1;
    border-color: rgb(85, 146, 217);
  }

  .bgc-blue {
    background-color: rgb(85, 146, 217);
  }

  .border-l-10 {
    border-left-width: 10px;
  }

  .border-l-7 {
    border-left-width: 7px;
  }

  .border-3 {
    border-width: 3px;
  }

  .border-b-1 {
    border-bottom: 1px solid #FFFFFF;
  }

  .bg-transparent {
    background: transparent !important;
  }

  .text-black {
    color: black !important;
  }

  .border-button {
    border: 1px solid rgb(148, 51, 145);
  }

  .fill-current-color {
    fill: rgb(148, 51, 145);
  }

  .w-66 {
    width: 66px;
  }

  .text-dark-blue {
    color: #26276A;
  }

}
