@media (min-width: 320px) {
  .container {
    max-width: 290px;
  }
}

@media (min-width: 414px) {
  .container {
    max-width: 400px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 660px;
  }
}

@media (min-width: 1000px) {
  .container {
    max-width: 1000px;
  }
}

