.repair {
  a {
    font-family: "RobotoRegular", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    color: #000000;
    @media (max-width: 425px) {
      font-size: 10px; } }

  span {
    font-family: "RobotoRegular", sans-serif;
    display: block;
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 26px;
    text-transform: uppercase;
    position: relative; }

  >span:after {
    content: "";
    position: absolute;
    height: 34px;
    width: 100vw;
    top: 0;
    left: 0;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    background-color: rgb(148, 51, 145);
    border-radius: 0.25rem;
    z-index: -100; }

  .cards {
    min-height: 265px;
    @media (max-width: 425px) {
      min-height: auto; } }


  .title {
    font-family: "RobotoRegular", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 26px;
    text-transform: uppercase;
    color: #173152; }

  p {
    font-family: "RobotoRegular", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #173152; }


  .border-2 a {
    font-family: "RobotoRegular", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 22px;
    text-transform: uppercase;
    color: #FFFFFF;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%); }

  .border-2 a:after {
    content: "";
    position: absolute;
    right: .5em;
    top: 50%;
    width: 25px;
    height: 24px;
    transform: translate(0, -50%);
    background-image: url(../img/shevron.png); } }
