.autorize {
  span {
    font-family: "RobotoRegular", sans-serif;
    display: block;
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 26px;
    text-transform: uppercase;
    position: relative;
    z-index: 20; }

  .container>span:after {
    content: "";
    position: absolute;
    height: 34px;
    width: 100vw;
    top: 0;
    left: 0;
    z-index: -100;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    background-color: rgb(85,146,217);
    border-radius: 0.25rem; }

  .-z-10 {
    z-index: -10; }

  .cards {
    width: 150px;
    height: 150px;
    @media (max-width: 768px) {
      width: auto;
      height: auto; } }

  .title {
      font-family: "RobotoRegular", sans-serif;
      font-style: normal;
      font-weight: bold;
      font-size: 22px;
      line-height: 26px;
      text-transform: uppercase;
      color: #173152; }

  p {
    font-family: "RobotoRegular", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #173152; }

  .border-2 a {
    font-family: "RobotoRegular", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 22px;
    text-transform: uppercase;
    color: #FFFFFF;
    top: 0;
    right: -2px;
    transform: translate(0, -50%); }

  .border-2 a:after {
    content: "";
    position: absolute;
    right: .5em;
    top: 50%;
    width: 25px;
    height: 24px;
    transform: translate(0%, -50%);
    transform: rotate(-90deg) translate(50%, 0%);
    background-image: url(../img/shevron.png); } }
