.main-banner {
  .main-banner-bg {
    @media (min-width: 769px) {
      background: rgb(212,238,251);
      background: linear-gradient(270deg, rgba(212,238,251,1) 0%, rgba(212,238,251,0) 100%); }
    @media (max-width: 768px) {
      background: rgba(148,38,143,1); } }

  .main-banner-title {
    min-height: 357px;
    background-image: url("../img/main-banner.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: right 50%;
    @media (max-width: 768px) {
      min-height: 320px;
      background-position: right bottom; }
    @media (max-width: 425px) {
      min-height: 200px;
      background-position: right bottom; } }




