header {
  .title-info {
    background-color: $dark-blue; }

  .title {
    font-family: "RobotoBold", sans-serif;
    font-weight: bold;
    line-height: 28px;
    color: $text-dark-blue; }

  .phone {
    span {
      font-family: "RobotoBold", sans-serif;
      font-weight: bold;
      font-size: 16px;
      line-height: 19px;
      color: $text-dark-blue; } }

  .contacts {
    .text-center {
      font-family: "RobotoRegular", sans-serif;
      font-weight: normal;
      font-size: 10px;
      line-height: 8px;
      color: #C1C0C0; } }

  .work-time {
    span {
      font-family: "RobotoRegular", sans-serif;
      font-size: 10px;
      line-height: 8px;
      color: #C1C0C0; }

    .days {
      span {
        font-family: "RobotoRegular", sans-serif;
        font-weight: 400;
        font-size: 10px;
        line-height: 12px;
        color: $black; } } }

  .menu {
    ul li a {
      font-family: "RobotoRegular", sans-serif;
      font-size: 12px;
      line-height: 12px;
      color: $black; } }

  .menu {
    ul li:last-child a {
      color: $white; } }

  ul li {
    border-left: 1px solid #C1C0C0; }

  ul li:nth-child(5) {
    border-right: 1px solid #C1C0C0; } }
